@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;700&display=swap");
/* Box sizing border-box for all elements */
*,
*:before,
*:after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

hr {
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

/* Links */
a {
  text-decoration: none;
  color: inherit;
}

/* Remove outline */
button,
:focus {
  outline: none;
}

/* Clearfix for floating elements */
.clearfix::before,
.clearfix::after {
  content: " ";
  display: table;
}

.clearfix::after {
  clear: both;
}

*,
body,
html,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
ol li,
ul li,
a,
a span {
  font-family: "IBM Plex Sans", sans-serif;
}

